import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch,
} from "react-router-dom";
import FirstImage from "../Routes/FirstImage";
import SecondImage from "../Routes/SecondImage";
import Header from "./Header";

export default () => (
  <Router>
    <Header />
    <Switch>
      <Route path="/uplusbaseball" exact component={FirstImage} />
      <Route path="/uplusbaseball/2" exact component={SecondImage} />
      <Redirect from="*" to="/uplusbaseball" />
    </Switch>
  </Router>
);
